<template>
    <div class="privacy-paragraphs">
        <template v-for="(p, i) in term.paragraphs">
            <div :key="i" style="margin-bottom: 20px">
                <div class="paragraph-title" v-html="p.title"></div>
                <privacy-scroller style="margin-bottom:10px"
                    :paragraph="p"
                />
                <div class="scroll-warning" v-if="!p.scrolled">
                    <span v-if="!p.scrolled" v-html="p.scrollText"></span>
                </div>
                <privacy-paragraph-flags :paragraph="p" />
            </div>
        </template>
    </div>
</template>

<script>
import PrivacyScroller from './PrivacyScroller'
import PrivacyParagraphFlags from './PrivacyParagraphFlags'
import {mapState} from 'puex'

export default {
  name: 'PrivacyParagraphs',
  components: {
    PrivacyScroller, PrivacyParagraphFlags
  },
  computed: {
    ...mapState(['term', 'scrollTillTheEnd'])
  },
  props: ['paragraph']
}
</script>

<style lang="scss">
    $warning-height : 20px;
    .privacy-paragraphs{
        .paragraph-title {
            font-size:12px;
            text-align: left;
            padding: 4px;
            font-weight: bold;
        }
        .scroll-warning{
            padding:8px;
            margin-bottom: 8px;
            color: red;
            text-align: center;
            font-weight: bold;
            font-size: 12px;
            border: 1px solid #e1e1e1;
            background-color:#f1f1f1;
            border-radius: 2px;
        }
    }
</style>



