<template>
    <tr class="privacy-flag" :class="{'not-scrolled':!pargraph.scrolled}">
        <td class="privacy-flag-checkbox"><input type="checkbox" v-model="flag.selected" :disabled="!pargraph.scrolled"></td>
        <td><span v-html="flag.text"></span><span v-if="flag.mandatory">*</span></td>
    </tr>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'PrivacyFlag',
  props: ['flag', 'pargraph'],
  watch: {
    'flag.selected' () {
      let $el = document.getElementsByClassName(this.flag.code)

      if ($el.length > 0) {
        $el = $el[0]
        $el.checked = this.flag.selected
        console.log(this.flag.code, $el.checked)
      }
    }
  },
  computed: {
    ...mapGetters(['allScrolled'])
  }
}
</script>

<style lang="scss">
    .privacy-flag{
        text-align: left;
        font-size:12px;
        td {
            vertical-align: center;
        }
        .privacy-flag-checkbox {
            width:20px;
        }
    }
    .privacy-flag.not-scrolled {
        color: silver;
    }
</style>



