<template>
        <vue-perfect-scrollbar class="scroll-area" v-scroll:debounce="{fn: onScroll, debounce: 200 }">
           <div class="privacy-scroller" v-html="paragraph.text"> </div>
        </vue-perfect-scrollbar>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'

export default {
  name: 'PrivacyScroller',
  components: {
    VuePerfectScrollbar
  },
  mounted () {
    this.checkScrolled()
  },
  data () {
    return {
      height: 200
    }
  },
  methods: {
    checkScrolled () {
      const isEnd = this.$el.scrollTop + this.height + 14 >= this.$el.scrollHeight
      // console.log(this.$el.scrollTop, this.height, this.$el.scrollHeight)
      if (isEnd) {
        this.$emit('scrolled', true)
        this.paragraph.scrolled = true
      }
      return isEnd
    },
    onScroll () {
      this.checkScrolled()
    }
  },
  props: ['paragraph']
}
</script>

<style lang="scss">
    $height : 200px;
    .privacy-scroller {
        height: $height;
        text-align: left;
        padding: 2px 10px;
        font-size:12px;
    }

    .scroll-area {
        border:1px solid silver;
        position: relative;
        margin: auto;
        width: auto;
        height: $height;
    }
</style>



